.sort-form {
    width: 30%;
}

.radio-btns {
    display: flex;
    flex-direction: column;
}

.sort-type {
    color: grey;
    font-size: .9em;
}

.radio-btns label {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: .25em;
    font-size: .9em;
    font-weight: 500;
}

.radio-btns p {
    margin-left: .25rem;
    margin-top: 1em;
    margin-bottom: .5em;
    font-weight: 500;
}